<template>

    <div class="popup-wrapper doc-view-popup-wrapper"
         :class="[opened ? 'opened' : '', classes]"
         :style="{'display': visible ? 'block' : 'none'}">

        <div class="popup-flex">

            <div class="doc-view-thumbs"></div>

            <div class="doc-view-block"
                 @scroll="docScroll">

                <div class="doc-view-content">

                    <div class="doc-view-main" @click.stop="''">
                        <template v-if="pdfView">
                            <div class="doc-view-main-content"
                                 :class="{'loading': !allPagesLoaded}">
                                <pdf v-for="(item, index) in numPagesPdf"
                                     :key="item"
                                     :src="blobPdfFile"
                                     :page="item"
                                     @page-loaded="checkPageLoaded(index)"/>
                            </div>
                        </template>
                        <template v-else>
                            <div class="doc-view-main-content">
                                <template v-for="(listItem, iter) in list">
                                    <template v-for="(listDoc, index) in listItem.list">
                                        <img :key="`${iter}-${index}`"
                                             :src="listDoc"
                                             alt=""/>
                                    </template>
                                </template>
                            </div>
                        </template>
                    </div>

                    <div class="doc-view-close" @click.stop="''">
                        <div class="doc-view-close-btn"
                             @click="closePopup">
                            <span class="icon-close-3"></span>
                        </div>
                    </div>

                </div>

                <div v-if="familiarizedBtn && pdfView ? allPagesLoaded : true"
                     class="doc-view-main-done">
                    <button class="btn btn-lg"
                            :disabled="familiarizedBtnDisabled"
                            @click="familiarizedSubmit">
                    </button>
                </div>

            </div>

            <div @click="closePopup"
                 class="popup-bg"></div>

        </div>
    </div>

</template>

<script>

    import pdf from 'vue-pdf'

    export default {
        name: "DocView",
        components: {pdf},
        props: {
            classes: [String, Object, Array],
            list: [Array, Object],
            familiarizedBtn: {
                type: Boolean,
                default: true
            }
        },
        data: () => ({
            visible: false,
            opened: false,
            active: 0,
            familiarizedBtnDisabled: true,
            pdfFile: '',
            numPagesPdf: '',
            pageLoaded: [],
            allPagesLoaded: false
        }),
        computed: {
            blobPdfFile() {
                return this.pdfFile
            },
            pdfView() {
                return this.list.format == 'pdf' || this.list.files[this.list.files.length - 1].format === 'pdf'
            }
        },
        methods: {
            closePopup() {
                this.opened = false
                setTimeout(() => {
                    this.visible = false
                    this.$emit('close-modal')
                    document.body.classList.remove('no-scroll');
                }, 400)
            },
            listQnt(i) {
                if (i == 1) {
                    return `${i} `
                }
                else if (i > 1 && i < 5) {
                    return `${i} `
                }
                else {
                    return `${i} `
                }
            },
            familiarizedSubmit() {
                if ( !this.familiarizedBtnDisabled ) {
                    this.$emit('familiarized-doc')
                    this.closePopup()
                }
                else {
                    alert(' c ')
                }
            },
            docScroll(e) {
                if ( this.pdfView ) {
                    if ( this.allPagesLoaded ) {
                        let block = e.target
                        if ( Math.ceil(block.scrollTop + block.clientHeight) >= Math.ceil(block.scrollHeight) ) {
                            this.familiarizedBtnDisabled = false
                        }
                    }
                }
                else {
                    let block = e.target
                    if ( Math.ceil(block.scrollTop + block.clientHeight) >= Math.ceil(block.scrollHeight) ) {
                        this.familiarizedBtnDisabled = false
                    }
                }
            },
            checkPageLoaded(index) {
                let allLoad = true
                this.pageLoaded[index] = true
                this.pageLoaded.forEach(item => item ? null : allLoad = false)
                this.allPagesLoaded = allLoad
            }
        },
        mounted() {

            document.body.classList.add('no-scroll');

            let rootThis = this
            this.visible = true

            if ( this.pdfView ) {
                this.$store.dispatch('getFile', this.list.id).then(resp => {
                    let file = URL.createObjectURL(resp)
                    this.pdfFile = pdf.createLoadingTask(file)
                    this.pdfFile.promise.then(pdf => {
                        this.numPagesPdf = pdf.numPages
                        this.pageLoaded = new Array(pdf.numPages).fill(false);
                    })
                })
            }

            setTimeout(() => {
                this.opened = true
            }, 10)

            this.$el.querySelectorAll('.close-modal').forEach(link => {
                link.addEventListener('click', () => {
                    rootThis.closePopup()
                })
            })

            const docView = document.querySelector('.doc-view-block')

            const clickOut = () => {
                this.closePopup()
            }

            docView.addEventListener('click', clickOut)
            this.$on('hook:beforeDestroy', () => docView.removeEventListener('click', clickOut))
        },
    }
</script>
